import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, InputType, WindowComponent } from 'summer';
import { CLASS_NAME, IndiceParticipacaoMunicipio } from '../../../../models/dtos/mlf/indiceParticipacaoMunicipio';
import CepUFIS from '../../../components/ger/cepUFIS/CepUFIS';

interface IndiceParticipacaoMunicipioEditViewProps extends EditViewProps<IndiceParticipacaoMunicipio> {}

const IndiceParticipacaoMunicipioEditView: FC<IndiceParticipacaoMunicipioEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);
  const formProps = useForm<IndiceParticipacaoMunicipio>({ defaultValues: selectedItem, mode: 'onBlur' });

  return (
    <WindowComponent title="Manutenção do Índice de Participação do Município">
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true, maxLength: 60 } }}
              isPrependLabel={true}
              id="codigo"
              label="Código"
            />
          </Col>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true } }}
              isPrependLabel={true}
              id="dataInicio"
              label="Data Início"
              type={InputType.DATE}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true, maxLength: 500 } }}
              isPrependLabel={true}
              id="descricao"
              label="Descrição"
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <CepUFIS
              id="cepUf"
              formProps={{ ...formProps, validation: { required: true } }}
              label="UF"
              isPrependLabel={true}
              placeholder="Selecione a UF"
            />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default IndiceParticipacaoMunicipioEditView;
